import React, { useEffect, useState } from "react";
import {
  Navbar,
  Container,
  NavDropdown,
  Nav,
  Button,
  Image,
} from "react-bootstrap";
import { NavLink, Link } from "react-router-dom";
import Logo from "../assets/logo.svg";
import "../styles/items.css";
const NavBar = () => {
  useEffect(() => {
    const handleScroll = (event) => {
      (window.scrollY>0 ? document.getElementById("navbar").classList.add("shadow-lg") :document.getElementById("navbar").classList.remove("shadow-lg") );
    };

    window.addEventListener("scroll", handleScroll);

    return () => {
      window.removeEventListener("scroll", handleScroll);
    };
  }, []);

  return (
    <Navbar
      id="navbar"
      collapseOnSelect
      expand="lg"
      style={{ backgroundColor: "#00B4D8" }}
      variant="transparent"
      sticky="top"
    >
      <Container className="text-light">
        <Navbar.Brand
          className="fs-3 fw-bolder text-dark"
          style={{ fontFamily: "Changa, sans-serif" }}
          href="#home"
        >
          <Image className="mb-2" style={{ height: "35px" }} fluid src={Logo} />
          Assistancya
        </Navbar.Brand>
        <Navbar.Toggle aria-controls="responsive-navbar-nav" />
        <Navbar.Collapse id="responsive-navbar-nav">
          <Nav className="me-auto">
            <Nav.Link href="#features">How to ?</Nav.Link>
            <Nav.Link href="#pricing">Why us ?</Nav.Link>
            <NavDropdown title="Other" id="collasible-nav-dropdown">
              <NavDropdown.Item href="#action/3.1">Team</NavDropdown.Item>
              <NavDropdown.Item href="#action/3.2">Contact</NavDropdown.Item>
              <NavDropdown.Item href="#action/3.3">Offers</NavDropdown.Item>
              <NavDropdown.Divider />
              <NavDropdown.Item href="#action/3.4">Demo</NavDropdown.Item>
            </NavDropdown>
          </Nav>
          <Nav>
            <Nav.Link className="me-md-2" style={{ textDecoration: "none" }}>
              {" "}
              <NavLink
                to="/Login"
                activeStyle
                style={{ textDecoration: "none" }}
              >
                <span
                  className=""
                  style={{ color: "rgb(3, 4, 94)", textDecoration: "none" }}
                >
                  {" "}
                  Login{" "}
                </span>
              </NavLink>
            </Nav.Link>

            <Nav.Link eventKey={2} className="" href="">
              <Button
                className="px-3"
                style={{ backgroundColor: "rgb(3, 4, 94)", marginTop: "-5px" }}
              >
                Free Trial
              </Button>
            </Nav.Link>
          </Nav>
        </Navbar.Collapse>
      </Container>
    </Navbar>
  );
};
export default NavBar;
